Vue.filter("formatResumeBase", function(value, fmt) {
  switch (value) {
    case "name":
      fmt = "";
      break;
    case "tel":
      fmt = "";
      break;
    case "Email":
      fmt = "";
      break;
    case "edu":
      fmt = "";
      break;
    case "address":
      fmt = "";
      break;
    case "salary":
      fmt = "";
      break;
    case "position":
      fmt = "";
      break;
    default:
      break;
  }
  return fmt;
});

